* {
	font-family: "Muli", sans-serif;
	margin: 0;
	outline: 1px dotted #888;
}

body {
	margin: 0;
	display: flex;
	flex-direction: column;
	width: 100vw; height: 100vh;
}

h1 {
	font-size: 50px;
}

.centered {
	display: flex;
	align-items: center;
	justify-content: center;
}

.left {
	display: flex;
	align-items: center;
	justify-content: flex-start;
}

.right {
	display: flex;
	align-items: center;
	justify-content: flex-end;
}

.space-between {
	padding: 10px 30px 10px 30px;
	display: flex;
	align-items: center;
	flex-direction: row;
	justify-content: space-between;
}

header {
	height: 125px;
}

.main-buttons {
	width: 40ch;
}

.main-buttons ul {
	display: flex;
	padding: 0; margin: 0;
	list-style-type: none;
	gap: 20px;
}

.main-buttons.left ul {
	display: flex;
	align-items: flex-start;
}

.main-buttons.right ul {
	display: flex;
	align-items: flex-end;
}

.title-area {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.setter {
	/* Maybe this needs a different font size. Not sure. */
}

main {
	height: 100%;
}

.left-panel {
	padding: 10px 30px 10px 0px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: space-between;

	height: 100%;
	width: 20vw; height: 100%;
}

.left-panel > ul >li::before {
	content: "â–¾ ";
	/* This can't be the right way to do this, can it? The intent is to have a drop-down tree menu. */
}

.left-panel * {
	padding: 0;
	list-style-type: none;
}

.left-panel form > div {
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.right-panel {
	height: 100%;
	width: 20vw; height: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: space-between;
}

.sudoku {
	display: flex;
	align-items: center;
	justify-content: center;

	/* maintain square aspect ratio */
	width: 70vh; height: 70vh;
}

footer {
	width: 100vw;
	height: 30px;

	font-size: 10px;
}
